.media-viewer-button {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.5;
}

.media-viewer-button-disabled {
  cursor: default;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.15;
}

.media-viewer-button:hover {
  opacity: 1;
  background: rgba(0, 0, 0, 0.2);
}
